body {
  margin: 0;
  background-color: black;
  color: grey;
  font-family: 'Courier New', Courier, monospace;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.container {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(10, 1fr);
  gap: 5px;
}

.cell {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  color: #444;
}

.highlight {
  color: green;
}

.highlight-yellow {
  color: yellow;
}

.highlight-white {
  color: white;
}